/* ==========================================================================
   button
   ========================================================================== */
/*
---
name: button
category:
  - Nav-items
tag:
  - button
---
work with both a and button tags
```html
<p>
  <a class="CG2-button" href="#">button:a element</a>
</p>
<p>
  <button class="CG2-button" href="#">button: button element</button>
</p>
```
with an icon
```html
<p>
  <a class="CG2-button" href="#"><span class="CG2-icon-pxg"></span>button</a>
</p>
```
use for Primary, Action, Setting
```html
<p>
  <a class="CG2-button CG2-button--primary" href="#">button</a>
</p>
<p>
  <a class="CG2-button CG2-button--action" href="#">button</a>
</p>
<p>
  <a class="CG2-button CG2-button--setting" href="#">button</a>
</p>
```
sizes
```html
<p>
  <a class="CG2-button CG2-button--thin" href="#">button</a>
</p>
<p>
  <a class="CG2-button CG2-button--large" href="#">button</a>
</p>
<p>
  <a class="CG2-button CG2-button--fullWidth" href="#">button</a>
</p>
```

disabled
`button``disabled`
(`a`)`aria-disabled="true"`
```html
<p>
  <a class="CG2-button" aria-disabled="true" href="#">button</a>
</p>
<p>
  <a class="CG2-button CG2-button--primary" aria-disabled="true" href="#">primary button</a>
</p>
<p>
  <a class="CG2-button CG2-button--action" aria-disabled="true" href="#">action button</a>
</p>
<p>
  <a class="CG2-button CG2-button--setting" aria-disabled="true" href="#">setting button</a>
</p>
```

progress
```html
<p>
  <a class="CG2-button CG2-button--progress" href="#">button</a>
</p>
```
*/
.CG2-button{
  color: #FFF;
  font-size: 14px;
  text-decoration: none;
  box-sizing: border-box;
  position: relative;
  display: inline-block;
  border: 1px solid #777;
  padding: 0.6em 1.8em;
  border-radius: 2px;
  background: #777;
  box-shadow: 0 0 0 rgba( 0, 0, 0, 0.1 );
  transition: all .2s;
  &:hover:not( [disabled] ):not( [aria-disabled="true"] ):not( .CG2-button--progress ),
  &:focus:not( [disabled] ):not( [aria-disabled="true"] ):not( .CG2-button--progress ){
    box-shadow: 0 0 3px #777;
    background: lighten( #777, 10% );
    transition: all .2s;
  }
  @include max-screen( $breakpoint-middle ) {
    font-size: 12px;
  }
  &.CG2-button--thin{
    padding: 0.4em 1.8em;
  }
  &.CG2-button--large{
    font-size: 20px;
    text-align: center;
    display: block;
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    @include max-screen( $breakpoint-middle ) {
      font-size: 18px;
      padding-top: .3em;
      padding-bottom: .3em;
      width: auto;
    }
  }
  &.CG2-button--fullWidth{
    display: block;
    width: 100%;
  }
  span[class^="CG2-icon"]{
    font-size: 20px;
    margin-right: 5px;
    &:first-child{
      margin-left: -.6em;
    }
  }
  // &.CG2-button--secondary{
  //   background: #10a040;
  // }
  &.CG2-button--primary{
    border-color: #10a040;
    background: #10a040;
    &:hover:not( [disabled] ):not( [aria-disabled="true"] ),
    &:focus:not( [disabled] ):not( [aria-disabled="true"] ){
      box-shadow: 0 0 3px #10a040;
      background: lighten( #10a040, 10% );
    }
  }
  &.CG2-button--action{
    border-color: #282828;
    background: #282828;
    &:hover:not( [disabled] ):not( [aria-disabled="true"] ),
    &:focus:not( [disabled] ):not( [aria-disabled="true"] ){
      box-shadow: 0 0 3px #282828;
      background: lighten( #282828, 10% );
    }
  }
  &.CG2-button--setting{
    border-color: #777;
    background: #777;
    &:hover:not( [disabled] ):not( [aria-disabled="true"] ),
    &:focus:not( [disabled] ):not( [aria-disabled="true"] ){
      box-shadow: 0 0 3px #777;
      background: lighten( #777, 10% );
    }
  }
  &[disabled],
  &[aria-disabled="true"],{
    cursor: not-allowed;
    border-color: #999;
    background: #DBDBDB;
  }
  &.CG2-button--progress{
    color: transparent;
    cursor: not-allowed;
    &:before{
      pointer-events: none;
      content: '';
      position: absolute;
      top: 5px;
      right: 5px;
      bottom: 5px;
      left: 5px;
      max-width: 100px;
      margin: auto;
      display: block;
      width: auto;
      background: url( '../img/CG2-button/loading.svg' ) no-repeat 50% 50%;
      background-size: 100% 100%;
      transform-origin: 50% 50%;
      animation: button--loading 1s linear infinite;
    }
  }
}

@-webkit-keyframes button--loading {
    0% { -webkit-transform: rotate(   0deg ); }
  100% { -webkit-transform :rotate( 360deg ); }
}
@keyframes button--loading {
    0% { transform: rotate(   0deg ); }
  100% { transform :rotate( 360deg ); }
}


/* ==========================================================================
   buttonContainer
   ========================================================================== */
/*
---
name: transparentButton
category:
  - Nav-items
tag:
  - button
---
work with both a and button tags
```html
<div style="background: rgba( 0, 0, 0, .5 );">
  <p>
    <a class="CG2-transparentButton" href="#">button:a element</a>
  </p>
  <p>
    <button class="CG2-transparentButton" href="#">button: button element</button>
  </p>
</div>
```

disabled
```html
<div style="background: rgba( 0, 0, 0, .5 );">
  <p>
    <a class="CG2-transparentButton" aria-disabled="true" href="#">button</a>
  </p>
  <p>
    <button class="CG2-transparentButton" disabled href="#">button</button>
  </p>
</div>
```

fav

.CG2-transparentButton__favText
```html
<div style="background: rgba( 0, 0, 0, .5 );">
  <p>
    <button class="CG2-transparentButton"><span class="CG2-icon-star"></span><span class="CG2-transparentButton__favText"></span></button>
  </p>
  <p>
    <button class="CG2-transparentButton CG2-transparentButton--favAdded"><span class="CG2-icon-star"></span><span class="CG2-transparentButton__favText"></span></button>
  </p>
</div>
```
*/
.CG2-transparentButton{
  color: #fff;
  font-size: 14px;
  line-height: 1.2;
  text-decoration: none;
  display: inline-block;
  padding: 6px 10px;
  border: 1px solid #fff;
  border-radius: 2px;
  background: rgba( 255, 255, 255, 0 );
  transition: all .3s;
  @include max-screen( $breakpoint-middle ) {
    font-size: 12px;
    padding: 4px 8px;
  }
  &:hover,
  &:focus{
    background: rgba( 255, 255, 255, 0.2 );
  }
  span[class^="CG2-icon"]{
    opacity: .5;
    margin: 0 4px 0 0;
    transition: all .3s;
  }
  &:hover span[class^="CG2-icon"],
  &:focus span[class^="CG2-icon"]{
    opacity: 1;
  }
  &[disabled],
  &[aria-disabled="true"]{
    color: #ccc;
    cursor: not-allowed;
    border-color: #ccc;
    background: rgba( 255, 255, 255, 0.2 );
    span[class^="CG2-icon"]{
      opacity: 1;
    }
  }
  &.CG2-transparentButton--favAdded{
    span[class^="CG2-icon"]{
      color: yellow;
      opacity: 1;
    }
    span.CG2-transparentButton__favText{
      color: rgba( 0, 0, 0, 0 );
      font-size: 0;
      line-height: 0;
      &:after{
        color: #fff;
        font-size: 14px;
        content:'';
        @include max-screen( $breakpoint-middle ) {
          font-size: 12px;
        }
      }
    }
  }
}

