*/

.CG2-seriesWeekHeader{
  margin: 80px 0 10px;
  @include max-screen( $breakpoint-middle ) {
    margin-left: -18px;
    margin-right: -18px;
  }
  .CG2-container__inner > &:first-child{
    margin-top: 20px;
    @include max-screen( $breakpoint-middle ) {
      margin-top: 0;
    }
  }
  a{
    @include resetLink();
  }
}
  .CG2-seriesWeekHeader__inner{
    display: table;
    width: 100%;
  }
    .CG2-seriesWeekHeader__prev{
      vertical-align: bottom;
      display: table-cell;
      width: 30%;
      a{
        padding-left: 20px;
        @include max-screen( $breakpoint-middle ) {
          padding-left: 16px;
        }
        &::before{
          @include icon( 'left' );
          font-size: 20px;
          position: absolute;
          bottom: .25em;
          left: 0;
          transition: transform .4s cubic-bezier(.3, 2, .6, 1 );
          @include max-screen( $breakpoint-middle ) {
            font-size: 14px;
            bottom: 0;
          }
        }
        &:hover::before,
        &:focus::before{
          transform: translateX( -4px ) scale( 1.2 );
          transition-duration: .4s;
        }
      }
    }
    .CG2-seriesWeekHeader__next{
      vertical-align: bottom;
      text-align: right;
      display: table-cell;
      width: 30%;
      a{
        padding-right: 20px;
        @include max-screen( $breakpoint-middle ) {
          padding-right: 16px;
        }
        &::after{
          @include icon( 'right' );
          font-size: 20px;
          position: absolute;
          bottom: .25em;
          right: 0;
          transition: transform .4s cubic-bezier(.3, 2, .6, 1 );
          @include max-screen( $breakpoint-middle ) {
            font-size: 14px;
            bottom: 0;
          }
        }
        &:hover::after,
        &:focus::after{
          transform: translateX( 4px ) scale( 1.2 );
          transition-duration: .4s;
        }
      }
    }
    .CG2-seriesWeekHeader__current{
      vertical-align: bottom;
      text-align: center;
      display: table-cell;
      width: 40%;
    }
    .CG2-seriesWeekHeader__prevInner,
    .CG2-seriesWeekHeader__nextInner,
    .CG2-seriesWeekHeader__currentInner{
      font-size: 0;
      line-height: 1.2;
      text-align: left;
      display: inline-block;
      a{
        text-decoration: none;
        display: block;
        position: relative;
      }
    }
      span.CG2-seriesWeekHeader__label{
        font-family: $fontSerif;
        display: block;
        font-size: 16px;
        vertical-align: baseline;
      }
      span.CG2-seriesWeekHeader__number{
        font-family: $fontCode;
        font-size: 32px;
        vertical-align: baseline;
        margin: 0 .1em;
        span.CG2-seriesWeekHeader__label + & {
          margin-left: 0;
        }
      }
      span.CG2-seriesWeekHeader__text{
        font-family: $fontSerif;
        font-size: 16px;
        vertical-align: baseline;
      }
    .CG2-seriesWeekHeader__current{
      span.CG2-seriesWeekHeader__label{ font-size: 20px; }
      span.CG2-seriesWeekHeader__number{ font-size: 64px; }
      span.CG2-seriesWeekHeader__text{ font-size: 32px; }
    }
    @include max-screen( $breakpoint-middle ) {
      .CG2-seriesWeekHeader__prev,
      .CG2-seriesWeekHeader__next{
        width: 22%;
        span.CG2-seriesWeekHeader__label{
          font-size: 12px;
        }
        span.CG2-seriesWeekHeader__number,
        span.CG2-seriesWeekHeader__text{
          display: none;
        }
      }
      .CG2-seriesWeekHeader__current{
        position: relative;
        bottom: -.4em;
        width: 56%;
        span.CG2-seriesWeekHeader__label{ font-size: 12px; }
        span.CG2-seriesWeekHeader__number{ font-size: 40px; }
        span.CG2-seriesWeekHeader__text{ font-size: 18px; }
      }
    }

