```

*/
.CG2-articlePagination{
  font-size: 14px;
  margin: 0 0 60px;
  @include max-screen( $breakpoint-middle ) {
    margin-bottom: 30px;
  }
  ul{
    list-style: none;
    display: table;
    width: 100%;
    padding: 0;
    margin: 0;
  }
  li{
    vertical-align: middle;
    position: relative;
    box-sizing: border-box;
    display: table-cell;
    width: 50%;
    @include max-screen( $breakpoint-small ) {
      display: block;
      width: auto;
    }
    &.CG2-articlePagination__prev a:before{
      @include icon( 'left' );
    }
    &.CG2-articlePagination__next a:before{
      @include icon( 'right' );
    }
    &.CG2-articlePagination__prev a:before,
    &.CG2-articlePagination__next a:before{
      line-height: 0;
      position: absolute;
      top: 50%;
    }
    @include min-screen( $breakpoint-small ) {
      &.CG2-articlePagination__prev{
        padding: 0 .5em 0 1.2em;
        a{
          margin-left: -1.2em;
          padding-left: 1.2em;
          &:before{
            left: 0;
          }
          &:hover:before,
          &:focus:before{
            left: -3px;
          }
        }
      }
      &.CG2-articlePagination__next{
        text-align: right;
        padding: 0 1.2em 0 .5em;
        a{
          margin-right: -1.2em;
          padding-right: 1.2em;
          &:before{
            right: 0;
          }
          &:hover:before,
          &:focus:before{
            right: -3px;
          }
        }
      }
    }
    @include max-screen( $breakpoint-small ) {
      padding-left: 1.2em;
      a{
        padding-left: 1.2em;
        margin-left: -1.2em;
        &:before{
          left: 0;
        }
      }
    }
  }
  a{
    @include resetLink();
    text-decoration: none;
    text-align: left;
    display: inline-block;
    &:after,&:before{
      transition: all .2s;
    }
  }
}

@include print () {
.CG2-articlePagination{
  display: none;
}
}

