*/
.CG2-formProgress{
  font-size: 20px;
  margin: 0 -40px 60px;
  @include max-screen( 1054px ) {
    font-size: 16px;
    margin-left: -20px;
    margin-right: -20px;
  }
  @include max-screen( $breakpoint-middle ) {
    font-size: 16px;
    margin: 10px 0 20px;
  }
  ol{
    list-style: none;
    table-layout: fixed;
    border-spacing: 40px 0;
    display: table;
    width: 100%;
    padding: 0;
    margin: 0;
    @include max-screen( $breakpoint-base ) {
      border-spacing: 20px 0;
    }
    @include max-screen( $breakpoint-middle ) {
      display: block;
      width: auto;
    }
  }
    li{
      opacity: 0.4;
      text-align: center;
      display: table-cell;
      padding: 0 0 10px;
      border-bottom: 1px solid #DBDBDB;
      @include max-screen( $breakpoint-middle ) {
        text-align: left;
        display: none;
        padding: 0 10px 5px;
        margin: 0 -10px;
      }
      &.CG2-formProgress__item--current{
        opacity: 1;
        @include max-screen( $breakpoint-middle ) {
          display: block;
        }
      }
    }
}







