.CG2-logo3d{
  position: relative;
  margin: 0 auto;
  background: #000;
}
  .CG2-logo3d__inner{
    height: calc( 100vh - 120px );
    background: #000;
    @include max-screen( $breakpoint-small ) {
      height: calc( 100vh - 30px );
    }
    img,
    canvas{
      display: block;
      margin: 0 auto;
    }
