    <div class="CG2-form__header">
      <h1></h1>
      <p>800<br></p>
    </div>
    <p class="CG2--alignCenter">800<br></p>
    <div class="CG2-form__table">
      <table>
        <col width="30%">
        <col width="70%">
        <tr>
          <th>
            <label></label>
            <span class="CG2-form__required"></span>
          </th>
          <td>
            <input type="text" placeholder=" ">
          </td>
        </tr>
        <tr>
          <th>
            <label></label>
            <span class="CG2-form__required"></span>
          </th>
          <td>
            <input type="text" placeholder="codegrid@pxgrid.com">
          </td>
        </tr>
        <tr>
          <th>
            <label></label>
            <span class="CG2-form__required"></span>
          </th>
          <td>
            <select>
              <option value="" class="CG2-form__placeholder"></option>
              <option></option>
              <option>CodeGrid</option>
              <option></option>
              <option></option>
            </select>
          </td>
        </tr>
        <tr>
          <th>
            <label></label>
            <span class="CG2-form__required"></span>
          </th>
          <td>
            <textarea placeholder="URL" cols="50" rows="6"></textarea>
          </td>
        </tr>
      </table>
    </div>
    <div class="CG2-buttonContainer">
      <ul>
        <li class="CG2-buttonContainer__item--full">
