*/

section.CG2-form{
  padding: 50px;
  margin: 80px -28px;
  background: #EEE;
  form{
    margin: 0;
  }
  p{}
  @include max-screen( $breakpoint-middle ) {
    padding: 25px 20px;
    margin: 40px -20px;
  }
}
    .CG2-form__header{
      margin: 0 0 30px;
      h1{
        font-size: 32px;
        font-family: $fontSerif;
        font-weight: normal;
        line-height: 1.6;
        text-align: center;
        margin: 0 0 20px;
      }
      p{
        text-align: center;
        margin: 16px 0 0;
      }
      @include max-screen( $breakpoint-small ) {
        margin-bottom: 16px;
        h1{
          font-size: 20px;
          margin-bottom: 16px;
        }
        p{
          text-align: left;
        }
      }
    }
    .CG2-form__table{
      table{
        width: 100%;
      }
        th{
          text-align: left;
          vertical-align: middle;
          padding: 8px;
        }
        td{
          padding: 8px;
        }
          input[type="text"],
          input[type="url"],
          input[type="email"],
          select,
          textarea{
            box-sizing: border-box;
            width: 100%;
            padding: 8px 14px;
            border: 1px solid #ccc;
            border-radius: 3px;
          }
          select{
            appearance: initial;
            // FIXME: SVG
            //  src/assets2/font/codegrid-icon/uE603-down.svg
            // data URL
            // <svg width="26" height="14" viewBox="0 0 26 14" xmlns="http://www.w3.org/2000/svg">
            //   <path fill="#777" d="M3 0L0 2.7 13 14 26 2.7 23 0 13 8.7"/>
            // </svg>
            background-image: url(data:image/svg+xml,%3Csvg%20width=%2226%22%20height=%2214%22%20viewBox=%220%200%2026%2014%22%20xmlns=%22http://www.w3.org/2000/svg%22%3E%3Cpath%20fill=%22%23777%22%20d=%22M3%200L0%202.7%2013%2014%2026%202.7%2023%200%2013%208.7%22/%3E%3C/svg%3E);
            background-repeat: no-repeat;
            background-size: 14px 8px;
            background-position: right 14px top 18px;
            background-color: #fff;
            @include max-screen( $breakpoint-middle ) {
              background-position: right 14px top 15px;
            }
          }
          ::placeholder {
            opacity: 1; // for Firefox
            color: #767676;
          }
          select.CG2-form__placeholder{
            color: #767676;
          }
      @include max-screen( $breakpoint-small ) {
        @include tableToBlock();
        th,
        td{
          padding: 0;
          width: 100%;
        }
        th{margin-bottom: 6px;}
        td{margin-bottom: 10px;}
          input[type="text"],
          input[type="url"],
          input[type="email"]{
            font-size: 16px;
          }
      }
    }
      span.CG2-form__required{
        color: #ff0000;
        font-size: 12px;
        font-weight: normal;
        line-height: 22px;
        margin-left: 10px;
        float: right;
        @include max-screen( $breakpoint-small ) {
          float: none;
          margin-left: 0;
        }
      }
    .CG2-form__legal{
      font-size: 12px;
      text-align: center;
      margin: 15px 0;
      a{
        color: inherit;
      }
      @include max-screen( $breakpoint-small ) {
        font-size: 8px;
        text-align: left;
        margin: 5px;
      }
    }
    .CG2-form__notice{
      font-size: 12px;
      text-align: center;
      margin: 15px 0;
      @include max-screen( $breakpoint-small ) {
        font-size: 8px;
        text-align: left;
        margin: 5px;
      }
    }

/* ==========================================================================
   CG2-flowExplanation
   ========================================================================== */
/*
---
name: flowExplanation
category:
  - Blocks/www
tag:
  - www-item
---

```html
<div class="CG2-flowExplanation">
  <section class="CG2-flowExplanation__item">
    <div class="CG2-flowExplanation__image">
      <img src="http://img.pxgrid.net/320x180" alt="" width="320" height="180">
    </div>
    <div class="CG2-flowExplanation__text">
      <h1></h1>
      <p></p>
    </div>
  </section>

  <section class="CG2-flowExplanation__item">
    <div class="CG2-flowExplanation__image">
      <img src="http://img.pxgrid.net/320x180" alt="" width="320" height="180">
    </div>
    <div class="CG2-flowExplanation__text">
      <h1></h1>
      <p></p>
    </div>
  </section>
</div>
```

*/
.CG2-flowExplanation{
  margin: 40px 0;
  counter-reset: CG2flowExplanation;
  @include max-screen( $breakpoint-small ) {
    margin: 30px 0;
  }
}
  section.CG2-flowExplanation__item{
    counter-increment: CG2flowExplanation;
    display: table;
    width: 100%;
    margin: 50px 0;
    @include max-screen( $breakpoint-small ) {
      display: block;
      margin: 30px 0;
    }
  }
    .CG2-flowExplanation__image{
      vertical-align: top;
      display: table-cell;
      img{
        display: block;
        border: 1px solid #DBDBDB;
      }
      @include max-screen( $breakpoint-middle ) {
        width: 40%;
        img{
          max-width: 100%;
          height: auto;
        }
      }
      @include max-screen( $breakpoint-small ) {
        display: block;
        width: auto;
        margin: 0 0 10px;
        img{
          margin: 0 auto;
        }
      }
    }
    .CG2-flowExplanation__text{
      vertical-align: top;
      display: table-cell;
      padding-left: 66px;
      h1{
        font-size: 24px;
        font-weight: normal;
        margin: 0 0 20px;
        &:before{
          content: counter( CG2flowExplanation );
          font-size: 32px;
          font-family: $fontCode;
          line-height: 24px;
          vertical-align: middle;
          display: inline-block;
          width: 1.2em;
          margin-left: -1.2em;
        }
      }
      p{}
      @include max-screen( $breakpoint-small ) {
        padding-left: 35px;
        h1{
          font-size: 12px;
          font-weight: bold;
          margin: 0 0 10px;
          &:before{
            font-size: 24px;
            font-weight: normal;
            line-height: 12px;
            width: 26px;
            margin-left: -26px;
          }
        }
        p{
          font-size: 12px;
        }
      }
    }



/* ==========================================================================
   CG2-faq
   ========================================================================== */
/*
---
name: faq
category:
  - Blocks/www
tag:
  - www-item
---

```html
<div class="CG2-faq">
  <div class="CG2-faq__question">
    PayPalWebMoney
  </div>
  <div class="CG2-faq__answer">
    <p>PayPalWebMoney</p>
    <aside class="CG2-faq__eg">
      <h1></h1>
      <ul>
        <li> : 864</li>
        <li> : 1,728864864</li>
      </ul>
    </aside>
  </div>
</div>
```

*/

.CG2-faq{
  font-size: 16px;
  margin: 40px 0;
  padding: 0 0 0 50px;
  @include max-screen( $breakpoint-middle ) {
    font-size: 12px;
    margin: 20px -10px;
    padding-left: 38px;
  }
}
  .CG2-faq__question{
    font-weight: bold;
    line-height: 1.5; //16(font-size) * 1.5(line-height) = 24(height)
    position: relative;
    min-height: 24px;
    padding: 8px 0;
    @include max-screen( $breakpoint-middle ) {
      min-height: 16px;
      padding-top: 6px;
      padding-bottom: 6px;
    }
    &:before{
      @include icon( 'question' );
      color: #282828;
      font-size: 40px;
      line-height: 40px;
      position: absolute;
      top: 0;
      left: -50px;
      @include max-screen( $breakpoint-middle ) {
        font-size: 28px;
        line-height: 28px;
        left: -38px;
      }
    }
  }
  .CG2-faq__answer{
    p{
      margin-top: 0;
      margin-bottom: 1em;
    }
    aside.CG2-faq__eg{
      padding: 20px 20px 20px 50px;
      margin: 20px 0 0 -50px;
      margin-left: -50px;
      background: #EEE;
      @include max-screen( $breakpoint-middle ) {
        padding: 10px 10px 10px 38px;
        margin-left: -38px;
      }
      h1{
        font-size: 100%;
        font-weight: bold;
        margin: 0;
      }
      ul{
        margin-top: 0;
        margin-bottom: 0;
        padding-left: 30px;
      }
      li{}
    }
  }


